import React, { Component } from "react";
import { Link } from "react-router-dom";

class LearningCategory extends Component {
  render() {
    return (
      <li>
        <Link to={{ pathname: this.props.url, state: { title: this.props.title } }}>
          <p>{this.props.title}</p>
        </Link>
      </li>
    );
  }
}

export default LearningCategory;



