import React, { Component } from "react";
import Login from "./containers/Login/Login";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import LearningCategories from "./containers/User/Learning/LearningCategories/LearningCategories";
import LearningVideos from "./containers/User/Learning/LearningVideos/LearningVideos";
import Aux from "./hoc/Auxilliary";

class App extends Component {
  componentDidMount() {}

  componentDidUpdate() {
    console.log("App js updated");
  }

  render() {
    return (
      <BrowserRouter basename={"/p"}>
        <Aux>
          <Switch>
            <Route
              path={`${process.env.PUBLIC_URL}/login`}
              exact
              render={props => <Login key="login0" page="login" {...props} />}
            />
            <Route path={`${process.env.PUBLIC_URL}/`} exact render={props => <LearningCategories {...props} />} />
            <Route path={`${process.env.PUBLIC_URL}/apps/:id`} exact render={props => <LearningVideos {...props} />} />
          </Switch>
        </Aux>
      </BrowserRouter>
    );
  }
}

export default App;



