import React, { Component } from "react";
import { Link } from "react-router-dom";

class RegisterContainer extends Component {
  render() {
    return (
      <div className="card card-container" id="signupbox">
        <form className="form-signin">
          <input type="text" id="inputName" className="form-control" placeholder="Name" required />
          <input type="email" id="inputSignupEmail" className="form-control" placeholder="Email" required />
          <input type="text" id="inputCountry" className="form-control" placeholder="Country" required />
          <input type="text" id="inputState" className="form-control" placeholder="State" required />
          <select className="form-control">
            <option>Select Country</option>
            <option>India</option>
            <option>China</option>
          </select>
          <select className="form-control">
            <option>Select State</option>
            <option>Gujarat</option>
            <option>Goa</option>
          </select>
          <select className="form-control">
            <option>Select preferred language</option>
            <option>English</option>
            <option>Chinese</option>
          </select>
          <input type="text" id="inputSignupPassword" className="form-control" placeholder="Password" required />
          <input type="text" id="inputConPassword" className="form-control" placeholder="Confirm Password" required />

          <button className="btn btn-lg btn-primary btn-block btn-signin" type="submit">
            Sign up
          </button>
          <Link to="/login">Sign In</Link>
        </form>
      </div>
    );
  }
}

export default RegisterContainer;



