import React, { Component } from "react";
import { Link } from "react-router-dom";

class LoginContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error: false,
      errorMessage: "",
      email: "",
      password: ""
    };
  }
  componentDidMount() {}

  componentDidUpdate() {
    console.log("Login container js updated");
  }

  handleSubmit = () => {
    if (this.state.email === "user@vishalavalani.com" && this.state.password === "testing123") {
      localStorage.setItem("token", "test");
      this.props.history.push("/");
    } else {
      this.setState({
        error: true,
        errorMessage: "Incorrect username and password"
      });
    }
  };
  handleEmailChange = event => {
    this.setState({ email: event.target.value });
  };

  handlePasswordChange = event => {
    this.setState({ password: event.target.value });
  };

  render() {
    let errorPlaceholder = null;
    console.log(this.state.error);
    if (this.state.error) {
      errorPlaceholder = <p className="alert alert-danger text-center">Username/Password incorrect</p>;
    }

    return (
      <div className="card card-container" id="loginbox">
        {errorPlaceholder}
        <form className="form-signin">
          <input
            type="email"
            id="inputEmail"
            onChange={this.handleEmailChange.bind(this)}
            value={this.state.email}
            className="form-control"
            placeholder="Email address"
            required
          />
          <input
            type="password"
            id="inputPassword"
            onChange={this.handlePasswordChange.bind(this)}
            value={this.state.password}
            className="form-control"
            placeholder="Password"
            required
          />

          <a onClick={this.handleSubmit} className="btn btn-lg btn-primary btn-block btn-signin pt-8">
            Sign in
          </a>
        </form>
      </div>
    );
  }
}

export default LoginContainer;



