import React, { Component } from "react";
import { Link } from "react-router-dom";
import Header from "../../Common/Header/Header";
import LearningCategory from "./LearningCategory/LearningCategory";
import Aux from "../../../../hoc/Auxilliary";
class LearningCategories extends Component {
  componentDidMount() {
    if (localStorage.getItem("token") !== "test") {
      this.props.history.push("/login");
    }
  }
  render() {
    return (
      <Aux>
        <div className="top_band">
          <Header {...this.props} />
        </div>
        <div className="container-fluid">
          <div className="row">
            <div className="col-xs-12">
              <div className="row choose_video_bg01">
                <div className="container">
                  <div className="col-xs-12">
                    <div className="row">
                      <div className="choose_video_gallery">
                        <ul>
                          <LearningCategory title="React JS" url="/apps/1" />
                          <LearningCategory title="Mobile Apps" url="/apps/2" />
                          <LearningCategory title=".NET Apps" url="/apps/3" />
                          <LearningCategory title="Sitefinity" url="/apps/4" />
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div id="bg">
          <img src="images/tf_choose_video_image01.jpg" alt="" />
        </div>
      </Aux>
    );
  }
}

export default LearningCategories;



