import React, { Component } from "react";
import Header from "../../Common/Header/Header";
import Aux from "../../../../hoc/Auxilliary";
import { Collapse } from "react-bootstrap";
import VideoItem from "./VideoItem/VideoItem";

class LearningVideos extends Component {
  componentDidMount() {
    if (localStorage.getItem("token") !== "test") {
      this.props.history.push("/login");
    }
    console.log(this.props);
  }
  state = {
    open1: false,
    open2: false,
    open3: false,
    open4: true,
    open5: false,
    open6: false,
    open7: false,
    open8: false,
    open9: false,
    open11: true,
    rightside: false
  };

  render() {
    const title = this.props.location.state.title;
    let panel = null;

    panel = (
      <div className="panel-group" id="accordion" role="tablist">
        <div className="panel panel-default">
          <Collapse in={this.state.open11}>
            <div id="collapseThree" role="tabpanel" aria-labelledby="headingOne">
              <div className="panel-body">
                <ul className="clips m-0 p-0">
                  <VideoItem title="Video 1" time="0m 41s" />
                  <VideoItem title="Video 2" time="0m 41s" />
                  <VideoItem title="Video 3" time="0m 41s" />
                  <VideoItem title="Video 4" time="0m 41s" />
                  <VideoItem title="Video 5" time="0m 41s" />
                </ul>
              </div>
            </div>
          </Collapse>
        </div>
      </div>
    );
    return (
      <Aux>
        <div className="top_band heighttop" id="toppart">
          <Header {...this.props} />
        </div>
        <div>
          <a id="menu-toggle-right" href="#" className="toggle" onClick={() => this.setState({ rightside: true })} />
          <div id="sidebar-wrapper-right" className={this.state.rightside ? "active" : ""}>
            <div className="sidebar_header">
              <h3>Heading name</h3>
              <a
                id="menu-close-right"
                href="#"
                className="toggle"
                onClick={() => {
                  this.setState({ rightside: false });
                }}
              />
            </div>
            <div className="clearfix" />
            <div className="right_menu_container">
              <img src="/images/tf_right_part_img01.jpg" alt="" />
            </div>
          </div>
        </div>
        <div className="container-fluid padrightzero">
          <div className="col-xs-12">
            <div className="row">
              <div className="col-lg-10 col-lg-push-2 col-md-9 col-md-push-3 col-sm-8 col-sm-push-4 padrightzero">
                <div style={{ position: "relative", display: "block", maxWidth: "100%" }}>
                  <video autoPlay loop src="/videos/cryoem.mp4" controls style={{ maxWidth: "100%", height: "auto" }} />
                </div>
              </div>
              <div className="col-lg-2 col-lg-pull-10 col-md-3 col-md-pull-9 col-sm-4 col-sm-pull-8">
                <div className="row">
                  <div className="row">
                    <div className="video_left_menu">
                      <div className="col-xs-12">
                        <div className="row">
                          <h5>{title} Portfolio</h5>
                        </div>
                        <div className="row">
                          <div className="card">
                            <div className="tab-content">
                              <div role="tabpanel" className="tab-pane active" id="home">
                                {panel}
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Aux>
    );
  }
}

export default LearningVideos;



